		position: relative;
		
		.radarChartBox {
			height: 75%;
			aspect-ratio: 1/1;
			position: relative;
			
			.radarChartBg {
				position: absolute;
				width: 100%;
				height: 100%;
			}
			
			.dynamicRadarSvg {
				width: 100%;
				height: 100%;
				position: absolute;
			}
			
			.itemName {
				position: absolute;
				height: 15.91%;
				font-size: 60%;
